<template>
  <div id="box" class="animate__animated animate__backInLeft">
    图像 :<input class="animate__animated animate__flip" id="fhie" type="file" ref="fileInput" @change="handleFileChange" /> <img class="animate__animated animate__backInRight" :src="imageUrl" alt="" v-if="imageUrl" /><br />
    <br />
    <el-input v-model="input" placeholder="请输入用户名"></el-input>
    <br />
    <br />
    <br />

    <el-input v-model="inpu" placeholder="请输入登录用户"></el-input> <br />
    <br />

    <el-radio v-model="radio" label="1">男</el-radio>
    <el-radio v-model="radio" label="0">女</el-radio> <br />
    <br />

    <el-input v-model="inp" placeholder="请输入手机号"></el-input> <br />
    <br />

    <el-button type="primary" @click="add()">确认添加</el-button>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data() {
    return { imageUrl: '', input: '', inpu: '', inp: '', radio: '1' }
  },
  methods: {
    add() {
      console.log(1111)
      let obj = { name: this.input, userid: this.inpu, sex: this.radio, iphone: this.inp }
      console.log(obj)
      axios
        .post('http://47.94.4.201/index.php/index/index/mycenter', obj)
        .then(res => {
          if (res.data.msg == '用户名重复') {
            console.log(res.data)
            this.$notify.error({
              title: '用户名重复',
              message: ''
            })
            return
          } else if (res.data.msg == '参数不能为空') {
            this.$notify({
              title: '不能为空',
              message: '',
              type: 'warning'
            })
            return
          } else {
            console.log(res.data)
            this.$notify({
              title: '成功',
              message: '',
              type: 'success'
            })
            this.$router.push({ path: '/abou', query: { userid: '2' } })
          }
        })
        .catch(error => {
          console.error(error) // 根据需要处理错误
        })
    },
    handleFileChange(event) {
      const file = event.target.files[0]
      const reader = new FileReader()
      reader.onload = e => {
        const base64Data = e.target.result
        const formData = new FormData()
        formData.append('imgurl', base64Data)
        axios
          .post('https://www.zzgoodqc.cn/index.php/index/upload/uploadimg', formData)
          .then(res => {
            const imagePath = res.data.data.url
            // 拼接接口域名或IP
            this.imageUrl = `http://47.94.4.201/${imagePath}`
            console.log(res.data)
          })
          .catch(error => {
            console.error(error)
            // 根据需要处理错误
          })
      }
      reader.readAsDataURL(file)
    },
    computed: {
      fullImageUrl() {
        return this.imageUrl
      }
    }
  }
}
</script>
<style>
#box {
  width: 500px;
  margin: auto;
}
#fhie {
  background: #409eff;
  color: #fff;
}
img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
</style>
